import 'vux/src/styles/reset.less';
import './register.scss'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Datetime from 'vux/src/components/datetime/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


Vue.use(ToastPlugin)

import { checkphone } from 'js/validate.js'

import { fetch, rap } from 'js/fetch.js'

let url = {
getSites: 'api/sites',
postRegister: 'api/register'
}

url = rap(url)
import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
name: '',//
phone: '',//
birthDay: '',//
childGender: [],//
genderList: [{ name: '', value: '1' }, { name: '', value: '2' }],
childLike: '',//
password: '',//
address: [],//
detailAddress: '',//
sites: [],//
isAgree: false,//
siteslist: [],//
isCheckTel: false,
isCheckPW: false,
addressData: ChinaAddressV4Data
},
computed: {
isDisabled() {

if (this.isAgree && this.name != '' && this.birthDay != '' && this.childGender.length != 0 && this.childLike != '' && this.address.length != 0 && this.detailAddress != '' && this.isCheckTel === true && this.isCheckPW === true && this.password != '' && this.phone != '') {
return false
} else {
return true
}
}
},
created() {

},
methods: {
register() {


fetch('post', url.postRegister, {
name: this.name,
password: this.password,
phone: this.phone,
child_gender: this.childGender[0],
child_like: this.childLike
}).then(res => {
if (res.status >= 200 && res.status <= 300) {
cookie.set('token', res.data.token)
window.location.href = "./buy_member.html"
} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})

},
checkTel(value) {
this.isCheckTel = checkphone(value)
return {
valid: this.isCheckTel,
msg: ''
}
},
checkPW(value) {
this.isCheckPW = value.length >= 6 && value.length <= 12 ? true : false
return {
valid: this.isCheckPW,
msg: '6-12'
}
},
getSites(boolean) {
if (boolean) {
console.log(this.address)

fetch('get', url.getSites, {
params: {
province_id: this.address[0],
city_id: this.address[1],
area_id: this.address[2]
}
}).then(res => {
if (res.status >= 200 && res.status <= 300) {
let array = []
res.data.forEach(element => {
array.push({ name: element.name, value: element.id + '' })
});
console.log(this)
console.log(array)
this.$set(this, 'siteslist', array);











} else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn'
})
}
})
}
},
onShow() {

},
onHide() {

},
onChange() {

}
},
components: {
Swiper,
Scroller,
Tab,
TabItem,
Sticky,
Flexbox,
FlexboxItem,
XInput,
Group,
XButton,
Cell,
CheckIcon,
PopupPicker,
XAddress,
Datetime
},
mixins: [mixin]
})



